import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import styled from "./AboutMe.module.css";
import Fullpage, {
  FullPageSections,
  FullpageSection,
} from "@ap.cx/react-fullpage";

import SkillChart from "./SkillChart";

function AboutMe() {
  // alert(".      .");

  const [introText, setIntroText] = useState("");
  const textIndex = useRef(0); // useRef  
  const timer = useRef(null); //   ref
  const [isLoaded, setIsLoaded] = useState(false);
  const [arrowOn, setArrowOn] = useState(false); //   
  const [chartsVisible, setChartsVisible] = useState(false); //   

  const fullText =
    " !     .     ,   .     ,     .";
  // ,    
  useEffect(() => {
    const typeText = () => {
      if (textIndex.current < fullText.length) {
        setIntroText((prev) => prev + fullText.charAt(textIndex.current));
        textIndex.current++;
        timer.current = setTimeout(typeText, 70);
      } else {
        //     

        clearTimeout(timer.current);
        setArrowOn(true);
      }
    };

    if (textIndex.current === 0) {
      //     
      setTimeout(typeText, 70);
    }

    return () => {
      //     
      clearTimeout(timer.current);
    };
  }, [fullText]);

  useEffect(() => {
    setIsLoaded(true);
  }, []);

  // (.) <br />  
  const formattedText = introText.replace(/[.,!]/g, "$&<br />");

  // section3      
  // Intersection Observer 
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const entry = entries[0];
        if (entry.isIntersecting) {
          setChartsVisible(true); //    
          observer.disconnect(); //     
        }
      },
      { threshold: 0.5 } // 50%    
    );

    const section3 = document.querySelector(`.${styled.AboutMe_section3}`);
    if (section3) {
      observer.observe(section3);
    }

    return () => {
      if (section3) {
        observer.unobserve(section3);
      }
    };
  }, []);

  return (
    <div className={styled.AboutMe_wrap}>
      <Fullpage>
        <FullPageSections>
          <FullpageSection style={{ height: "90vh", backgroundColor: "#000" }}>
            <div className={styled.AboutMe_q_mark}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 100 100"
                width="100"
                height="100"
              >
                <circle cx="50" cy="50" r="25" fill="lightgray" />

                <text
                  x="37"
                  y="66"
                  font-size="45"
                  font-family="Arial"
                  fill="#fff"
                >
                  ?
                </text>
              </svg>
              <p className={styled.AboutMe_q_text}>
                <span>*     .</span>
                <br />
                 (index)  
                <br />
                Fullpage
                <br />
                setTimeout
                <br />
                Chart.register
                <br />
                Media Query ( )
                <br />
                IntersectionObserver
                <br /> JAVA ()
                <br />
              </p>
            </div>
            <section className={styled.AboutMe_section1}>
              {/*    */}
              <img
                className={`${styled.AboutMe_section1_back_img} ${
                  isLoaded ? styled.loaded : ""
                }`}
                src={require("../img/section1_back.webp")}
                alt="Background"
              />
              {/*   */}
              <img
                className={styled.AboutMe_section1_img}
                src={require("../img/section1_front.png")}
                alt="Welcome"
              />

              <h2 className={styled.AboutMe_top_title}>
                {/* {introText} */}
                <span dangerouslySetInnerHTML={{ __html: formattedText }} />
              </h2>

              {/*   (   ) */}
              {arrowOn && (
                <img
                  className={styled.arrow}
                  src={require("../img/arrow.png")} //   
                  alt="Scroll Down"
                />
              )}
            </section>
          </FullpageSection>

          <FullpageSection
            style={{ height: "90vh", backgroundColor: "#F7F7F7" }}
          >
            <section className={styled.AboutMe_section2}>
              <h3 className={styled.section2_title}> </h3>
              <ul className={styled.image_list}>
                <li>
                  <Link to="/job1">
                    <img src={require("../img/job1_1.jpg")} alt="" />
                    <h4>   </h4>
                  </Link>
                </li>
                <li>
                  <Link to="/job2">
                    <img src={require("../img/job2_1.jpg")} alt="" />
                    <h4>  </h4>
                  </Link>
                </li>
                <li>
                  <Link to="/job3">
                    <img
                      src={require("../img/job3_1.jpg")}
                      alt=" "
                    />
                    <h4> </h4>
                  </Link>
                </li>

                <li>
                  <Link to="/job4">
                    <img
                      src={require("../img/job4_1.jpg")}
                      alt=" "
                    />
                    <h4>   </h4>
                  </Link>
                </li>
              </ul>
            </section>
          </FullpageSection>

          <FullpageSection
            style={{ height: "90vh", backgroundColor: "#333", color: "white" }}
          >
            <section className={styled.AboutMe_section3}>
              <h3 className={styled.section3_title}> </h3>
              <ul className={styled.image_list}>
                {chartsVisible && ( // chartsVisible true   
                  <>
                    <li>
                      <SkillChart
                        labels={[
                          "",
                          "",
                          "",
                          "",
                          "",
                          "  ",
                        ]}
                        dataPoints={[8, 7, 7, 8, 8, 10]}
                        title=" "
                      />
                    </li>
                    <li>
                      <SkillChart
                        labels={[
                          "HTML",
                          "CSS",
                          "JavaScript",
                          "JQuery",
                          "SwiperSlide",
                          "",
                        ]}
                        dataPoints={[10, 9, 4, 7, 9, 8]}
                        title=" "
                      />
                    </li>
                    <li>
                      <SkillChart
                        labels={[
                          "Java",
                          "Spring",
                          "Oracle",
                          " ",
                          "JQuery",
                          "CRUD",
                        ]}
                        dataPoints={[8, 8, 7, 9, 9, 7]}
                        title=" "
                      />
                    </li>

                    <li>
                      <SkillChart
                        labels={[
                          "Java",
                          "Spring",
                          "Oracle",
                          " ",
                          "React",
                          "CRUD",
                        ]}
                        dataPoints={[8, 8, 7, 10, 6, 7]}
                        title=""
                      />
                    </li>
                  </>
                )}
              </ul>
            </section>
          </FullpageSection>
        </FullPageSections>
      </Fullpage>
    </div>
  );
}

export default AboutMe;
