import React from "react";
import { Radar } from "react-chartjs-2";
import {
  Chart,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";

Chart.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

const SkillChart = ({ labels, dataPoints, title }) => {
  const data = {
    labels: labels,
    datasets: [
      {
        label: title,
        data: dataPoints,
        backgroundColor: "rgba(173, 216, 230, 0.2)", //    (rgba)
        borderColor: "rgba(0, 191, 255, 1)", //   ( )
        borderWidth: 2,
        pointBackgroundColor: "rgba(0, 191, 255, 1)", //  
        pointBorderColor: "#fff", //   
        pointHoverBackgroundColor: "#fff",
        pointHoverBorderColor: "rgba(0, 191, 255, 1)",
      },
    ],
  };

  const options = {
    scales: {
      r: {
        angleLines: { display: true },
        suggestedMin: 0,
        suggestedMax: 10,
        grid: {
          color: "rgba(255, 255, 255, 0.2)", //    ( )
        },
        pointLabels: {
          font: {
            size: 16, //   
          },
          color: "#fff", //    ()
        },
      },
    },
    plugins: {
      legend: {
        labels: {
          color: "#fff", //   
        },
      },
    },
  };

  return <Radar data={data} options={options} />;
};

export default SkillChart;
