import React, { useState } from "react";
import style from "./Experience.module.css";

function Experience() {
  const [isOpen1, setIsOpen1] = useState(false); //    
  const [isOpen2, setIsOpen2] = useState(false); //    

  const toggleOpen1 = () => {
    setIsOpen1(!isOpen1); //     
  };

  const toggleOpen2 = () => {
    setIsOpen2(!isOpen2); //     
  };

  return (
    <div className={style.Experience_wrap}>
      <div>
        <h3></h3>

        <ul>
          <li>
            <div className={style.Experience_box}>
              <div className={style.Experience_days}>2023.03 ~ 2024.05</div>
              <div>
                <p>
                  
                  <span>  /  </span>
                </p>

                <div
                  className={
                    isOpen1 ? style.Experience_show : style.Experience_hide
                  }
                >
                  <p>
                    HTML5
                    <br />
                    CSS
                    <br />
                    JQuery <br />
                    JavaScript
                    <br />
                    JAVA
                    <br />
                    Spring
                    <br />
                    Mybatis
                    <br />
                    JSP
                    <br />
                    Oracle
                    <br />
                    AUI Grid
                    <br />
                     
                    <br />
                    <hr />
                  </p>

                  <p>
                     QMS :   , 
                    <br />
                     QMS :   , 
                    <br />
                     QMS :   , 
                    <br />
                     LIMS : Audit Trail    <br />
                  </p>
                </div>
              </div>
            </div>
            <div
              className={style.Experience_under_line}
              onClick={toggleOpen1} //      
            >
              <p></p>
              <span>
                <svg
                  className={isOpen1 ? style.rotate : ""}
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 100 100"
                  width="100"
                  height="100"
                >
                  <polyline
                    points="10,30 50,70 90,30"
                    fill="none"
                    stroke="black"
                    strokeWidth="10"
                    strokeLinecap="round"
                  />
                </svg>
              </span>
              <p></p>
            </div>
          </li>

          <li>
            <div className={style.Experience_box}>
              <div className={style.Experience_days}>2019.11 ~ 2022.04</div>
              <div>
                <p>
                  
                  <span>  / 2  </span>
                </p>

                <div
                  className={
                    isOpen2 ? style.Experience_show : style.Experience_hide
                  }
                >
                  <p>
                    HTML5
                    <br />
                    CSS
                    <br />
                    JQuery <br />
                    JavaScript
                    <br />
                    Media Query
                    <br />
                    SwiperSlide
                    <br />
                    <hr />
                  </p>
                  <p>
                         
                    <br />
                      pc
                    <br />
                     mobile   pc,mobile   
                    <br />
                  </p>
                </div>
              </div>
            </div>
            <div
              className={style.Experience_under_line}
              onClick={toggleOpen2} //      
            >
              <p></p>
              <span>
                <svg
                  className={isOpen2 ? style.rotate : ""}
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 100 100"
                  width="100"
                  height="100"
                >
                  <polyline
                    points="10,30 50,70 90,30"
                    fill="none"
                    stroke="black"
                    strokeWidth="10"
                    strokeLinecap="round"
                  />
                </svg>
              </span>
              <p></p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Experience;
